// Media Manager

.fileuploader {
    display: flex;
    flex-direction: column;
    &__wrapper {
        display: flex;
        width: 90px;
        height: 90px;
        position: relative;
        margin-bottom: 1rem;
    }
    &__list {
        flex: 1;
    }
    &__items {
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        li {
            list-style: none;
        }
    }
    &__item {
        padding-bottom: 20%;
        width: calc(25% - 15px);
        margin-bottom: 1rem;
        margin-right: 1rem;
        list-style: none;
        position: relative;
        &:hover {
            .fileuploader__action {
                transform: scale(1);
            }
        }
    }
    &__item-image {
        display: flex;
        position: relative;
        overflow: hidden;
        border-radius: .25rem;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: .25rem;
        background: #eee;
        img {
            max-width: initial;
            max-height: 100%;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translateX(-50%) translateY(-50%);
        }
        &::after {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            background: rgba(0,0,0,0.3);
            z-index: 1;
            opacity: 0;
            transition: opacity 250ms ease-in-out;
        }
        &:hover {
            &::after {
                opacity: 1;
            }
        }
    }
    &__action {
        color: var(--danger);
        padding: 0;
        margin: 0;
        line-height: 1;
        transform: scale(0);
        //transition: $transition-base;
        transition: all 250ms ease-in-out;
        border: 1px solid white;
        border-radius: 50%;
        background: white;
    }
    &__action-remove {
        color: var(--danger);
        padding: 0;
        line-height: 1;
    }
    &__icon-remove {
        font-size: 1.28rem; 
    }
}
.actions-holder {
    position: absolute;
    top: 0.2rem;
    right: 0.2rem;
    z-index: 2;
}
.fileuploader--theme-avatar {
    flex-direction: column;
    align-items: center;
    padding: 3rem;
    .fileuploader__item-image {
        border-radius: 50%;
    }
    .fileuploader__items {
        height: 100%;
    }
    .fileuploader__item {
        width: 100%;
        height: 100%;
        margin: 0;
    }
}
.file-type {
    &--doc, &--pdf {
        .fileuploader__item-image {
            img {
                width: 60px;
            }
        }
    }
}