.navbar {
    display: block;

    @include media('<tablet') {
        padding: 0;
    }

    &-nav {
        width: 100%;

    }
}



// .nav-item {
//     .nav-link {
//         @include media('>=tablet') {
//             padding: 1rem 1.6rem !important;
//         }
//     }
// }

.navbar-brand {
    @include media('<tablet') {
        margin-right: 0;
    }
    &.handheld-on {
        background: theme(bg, dark);
        img {
            @include media('<tablet') {
                height: 42px;
            }
        }
    }
}

.navbar-toggler {
    background: theme(bg, dark);
    border-radius: 0;
    color: theme(text, light);
    font-size: 1.7rem;
    padding: .25rem 1.6rem;
    &-icon {
        height: auto;
    }
    &:focus {
        outline: none;
    }
}

.navbar-collapse {
    @include media('<tablet') {
        position: fixed;
        top: 48px;
        left: 0;
        padding: 2.5rem;
        height: 100vh !important;
        background: theme(bg, dark);
        width: 250px;
        z-index: 999;
        transform: translateX(-100%);
        transition: all 0.3s ease;
    }
}

.navbar-collapse.show {
    transform: translateX(0px);
}

