.tabClose {
    //border-bottom-left-radius: 3px;
    //border-top-left-radius: 3px;
    //background: #fff;
    visibility: hidden;
    font-size: 10px;
    //padding: 2px;
    position: absolute;
    top: 0;
    right: 2px;
    //box-shadow: 1px 2px 10px rgba(0,0,0,.2);
    span {
        display: inline-block;
        padding: 2px;

        &:hover {
            &.fa-trash {
                color: $red;
            }

            &.fa-cog {
                color: theme(button,primary);
            }
        }
    }
}
.tab-controls {
    &.show{
        display:flex !important;
    }
    .icon {
        height: auto;
        width: auto;
        color: #5c5c5c;
    }
}

// top tab bar
div #tabs.ui-tabs-nav {
    background: #fff;
    margin-left: 30px;
    margin-bottom: 0;
    position: relative;
    z-index: 3;
    display: flex;
    flex-direction: row;
    transition: all 0.3s ease 0s;

    @include media('>desktop') {
        width: calc(100% - 255px);
        float: right;
    }

    

    .ui-state-default {
        height: 40px;
        float: left;
        list-style: none;
        position: relative;

        .tab-control {
        }

        &:hover {
            background-color: #f8f8f8;

            &:after {
                content: "";
                height: 3px;
                background: theme(hover-bg, dark);
                height: 3px;
                width: 100%;
                display: block;
                margin-top: -2px;
            }
        }


        .ui-tabs-anchor {
            padding: 0 3rem;
            height: 40px;
            display: -ms-flexbox;
            display: flex;
            align-items: center;
            white-space: nowrap;

            &:hover {
                text-decoration: none;
            }
        }

        > .control-tools {
            border: 0;
        }

        input {
            border: none;
            padding: 3px 6px;
            background: transparent;

            &.bordered {
                border: 1px solid #ced4da;
            }
        }

        &.ui-state-hover {
            .tab_input {
                background: #f2f2f2;
            }

            .tabClose {
                visibility: visible;
            }
        }

        &.ui-tabs-active {
            &:after {
                content: "";
                height: 3px;
                background: theme(button, primary);
                height: 3px;
                width: 100%;
                display: block;
                margin-top: -2px;
            }
        }
    }
}
.ui-tabs-nav-add {
    white-space: nowrap;
    height: 40px;
    display:flex;
    align-items:center;
}


