.stage-wrap {
    margin-right: -5px;
    margin-left: -5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    align-items: flex-start;
    align-content: flex-start;

    .fb-columns {
        display: flex;
        flex-wrap: wrap;
        padding: 25px 10px 10px;

        &.form-row > [class*=col-] {
            padding-bottom: 10px;
        }
    }

    &.ui-sortable, .ui-sortable {
        min-height: 100px;
    }

    li.ui-sortable-helper {
        position: absolute !important;
        top: 0;
    }
}

