.stage-wrap>.form-field {
    &.editing {
        &:before {
            content: "";
            position: fixed;
            top: 0;
            left: 0;
            width: 100vw;
            height: 100vh;
            background: rgba(0, 0, 0, 0.1);
            z-index: 9998;
            display:none;
            @include media('>desktop') {
                display: block;
            }
        }
    }
}

