.newInputTab {
    border: 1px solid #ccc;
    padding-bottom: 56px;
    position: relative;
    min-height: 150px;
    min-width: 335px;

    &:before {
        content: '';
        height: 100%;
        width: 1px;
        background: #ccc;
        display: block;
        position: absolute;
        top: 0;
        left: calc(50% - 1px);
        z-index: 1;
    }

    .ui-tabs {
        display: flex;

        &-nav {
            width: 50%;
            float: initial;
            padding: 40px 0 0;
            position: relative;

            &:before {
                content: "Main List";
                position: absolute;
                top: 9px;
                left: 9px;
                font-weight: bold;
            }

            li {
                border-radius: 0 !important;
                list-style: none;
                padding: 0 !important;
                position: relative;
                margin-bottom: 0 !important;

                a {
                    display: block;
                    position: relative;

                    input {
                        border: none !important;
                        border-bottom: 1px solid #ccc !important;
                        max-width: initial;
                        width: 100% !important;
                        padding-right: 24px !important;
                        background: #f2f2f2 !important;



                        &:focus {
                            outline: none;
                            box-shadow: inset 2px 2px 5px rgba(0,0,0,0.2);
                            background: #f2f2f2 !important;
                        }
                    }
                }

                &:first-child {
                    a {
                        input {
                            border-top: 1px solid #ccc !important;
                        }
                    }
                }

                .tabClose {
                    position: absolute;
                    top: 0px;
                    right: 0px;
                    cursor: pointer;
                }

                &:hover {
                    .tabClose {
                        visibility: visible;
                    }
                }

                &.ui-state-active {
                    a {
                        input {
                            background: #fff !important;
                        }
                    }

                    &:before {
                        content: '';
                        height: 100%;
                        width: 1px;
                        background: #fff;
                        display: block;
                        position: absolute;
                        top: 0;
                        right: 0;
                        z-index: 2;
                    }
                }
            }
        }

        &-panel {
            width: 50%;
            padding-top: 40px;
            position: relative;

            &:before {
                content: "Sub List";
                position: absolute;
                top: 9px;
                left: 9px;
                font-weight: bold;
            }

            .sub_tab_input {
                padding-right: 24px !important;
            }
        }
    }

    .ui-tabs-active:after {
        display: none !important;
    }

    input {
        width: 100%;
        padding: 6px 8px !important;
    }

    .subliRev {
        visibility: hidden;
        position: absolute;
        top: 16px;
        right: 16px;
        cursor: pointer;
        font-size: 16px;

        &:hover {
            color: red;
        }
    }

    li:hover {
        .subliRev {
            visibility: visible;
        }
    }

    button {
        border: none;
        background: #6c757d;
        border-radius: 3px;
        margin: 5px;
        padding: 3px 8px;
        color: #fff;
        cursor: pointer;

        &:hover {
            background: #5a6268;
        }
    }
}

