.fb-popup {
    position: fixed;
    z-index: 99999999999999999;
    top: 0px;
    width: 100%;
    height: 100%;
    left: 0px;
    display: none;
    text-align: center;
    background-color: rgba(95, 95, 95, 0.5);

    .select-option-head {
        padding-right: 17px;
    }

    .validation_rule_wrapper {
        overflow-y: hidden;
        height: 100px;
        padding-right: 17px;

        &:hover {
            overflow-y: scroll;
            padding-right: 0;
        }
    }

    .target_option_wrapper {
        overflow-y: hidden;
        height: 100px;
        padding-right: 17px;

        &:hover {
            overflow-y: scroll;
            padding-right: 0;
        }
    }

    .field-validation-wrapper {
        margin-bottom: 10px;
        position: relative;
        padding: 0 .825rem;
        border: 1px solid #ccc;
        background-color: #fafafaa8;

        label {
            margin-bottom: 0;
        }

        .muted-field {
            margin-top: 5px;
            height: 100%;
            width: 95px;
            border: 1px solid #ced4da;
            background-color: #F4F4F4;
            border-radius: 3px;
            padding: .5rem 1rem;
            text-overflow: ellipsis;
            overflow: hidden;
       
        }

    .field-controller {
        background-color: #F4F4F4;
        padding: .5rem;
        position: absolute;
        top: 0;
        right: 0;

        .icon {
            color: #5c5c5c;

            &:hover {
                .fa-copy {
                    color: theme(accent,primary);
                }

                .fa-times-trash {
                    color: $red;
                }
            }
        }
    }

    .form {
        &-row {
            margin: 10px 0;
        }
    }
}

    .field-data-clone {
        .field-controller {

            .icon {
                color: #5c5c5c;

                &:hover {
                    .fa-copy {
                        color: theme(accent,primary);
                    }

                    .fa-trash {
                        color: $red;
                    }
                }
            }
        }
    }

    .btn-invalid {
        font-weight: bold;
        font-size: 16px;
        border-radius: 3px;
        background-color: #FF8484;
        color: #fff;
    }
