.fb-card {
    list-style: none;
    margin: 0px;
    padding: 0px;
    background-color: #F5F5F5;
    padding: 15px;
    padding-top: 16px;

    .form-row:last-child .form-group {
        margin: 0;
    }

    .form-group {
        .slider-text {
            padding-left: 15px;
        }
    }

    .table-scrollable {
        display: block;
        position:relative;
        thead {
            padding-right:17px;
            display: block;
            width: 100%;

            tr {
                display: block;

                th {
                    word-break: break-all;
                    line-height: 1.2;
                    padding: 0.5rem;
                }
            }
        }

        tbody {
            padding-right:17px;
            display: block;
            height: 100px;
            width: 100%;
            overflow:hidden;

            &:hover {
                padding-right:0;
                overflow-y: scroll;
            }

            td {
                padding: 0.5rem;
            }
        }

        .field-controller {
            .icon i {
                display: block;
                padding: 2px;
            }
        }
    }
}

.fb-card > li {
    list-style: none;
    margin: 0px;
    border: 1px solid #cdcdcd;
    border-bottom: 0px;
}

.fb-card > li:last-child {
    border-bottom: 1px solid #cdcdcd;
}

.fb-card > li > header {
    background-color: #F5F5F5;
    height: 38px;
    display: flex;
    color: #707070;
    padding: 8px 10px 8px 25px;
    position: relative;
    cursor: pointer;
}

.fb-card > li.active > header:before {
    transform: rotate(90deg);
    left: 8px;
}

.fb-card > li.active > section {
    border-top: 1px solid #DDDDDD;
}

.fb-card > li > section {
    background-color: #FFF;
    height: auto;
    display: none;
    padding: 10px 10px 15px 10px;
    border-top: 1px solid #cdcdcd;
}

    

    .fb-card > li.active > section {
        display: block;
    }

    .fb-card > li > header:before {
        position: absolute;
        content: "\f054";
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        left: 11px;
        top: 13px;
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
        display: inline-block;
        font-style: normal;
        font-variant: normal;
        text-rendering: auto;
        line-height: 1;
        transition: all 0.25s ease-in;
    }
    /*default visible sliders */
    .fb-card {
        .switch {
            margin: 0;
            background: none;
            position: relative;
            display: inline-block;
            width: 28px;
            height: 16px;
        }

        .switch input {

            opacity: 0;
            width: 0;
            height: auto;
            &.form-control{
                height:16px;
            }
        }

        .slider {
            height: 16px;
            width: 100%;
            position: absolute;
            cursor: pointer;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: #fff;
            -webkit-transition: .4s;
            transition: .4s;
            border: 1px solid #cecece;
        }

        .slider:before {
            position: absolute;
            content: "";
            height: 12px;
            width: 12px;
            left: 1px;
            bottom: 1px;
            background-color: #818181;
            -webkit-transition: .4s;
            transition: .4s;
        }

        input:checked + .slider {
            border: 1px solid #2196F3;
            background-color: #2196F3;
        }

        input:focus + .slider {
            box-shadow: 0 0 1px #2196F3;
        }

        input:checked + .slider:before {
            -webkit-transform: translateX(12px);
            -ms-transform: translateX(12px);
            transform: translateX(12px);
            background: #fff;
        }
        /* Rounded sliders */
        .slider.round {
            border-radius: 34px;
        }

        .slider.round:before {
            border-radius: 50%;
        }
    }
    
//*********fb-popup-close
.fb-close {
    position: absolute;
    right: 14px;
    top: 0px;
 /* width: 30px;
    height: 32px;
 */
    opacity: 0.3;
    padding: 12px 10px;
    border: 1px solid #000;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    border-top: 0;

    &:hover {
        opacity: 1;
        border: 1px solid $red;
        border-top: none;

        &:before, &:after {
            background-color: $red;
        }
    }

    &:before, &:after {
        position: absolute;
        top: 6px;
        left: 9px;
        content: ' ';
        height: 12px;
        width: 2px;
        background-color:#000;
        ;
    }

    &:before {
        transform: rotate(45deg);
    }

    &:after {
        transform: rotate(-45deg);
    }
}

