        font-size: 4rem;
    }
    &--img {}
}

.module-content {
    border: 2px solid var(--primary);
    background-color: #F9FEFF;
    padding: 1.25rem;
    margin-bottom: 1.5rem;
    border-radius: 0.25rem;
}
.module-tab {
    border: 1px solid rgba(0, 0, 0, 0.125)!important;
    position: relative;
    text-decoration: none!important;
    transition: all 250ms linear;
    color: #2b2b2b;
    border-radius: 0.25rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 1.5rem;
    &:hover, &.show {
        background-color: #F9FEFF;
        border-color: transparent;
        box-shadow: 0 0 0 2px var(--primary);
        color: #2b2b2b;
    }
}
.module-tab .fa-check-circle {
    position: absolute;
    font-size: 1.142857142857143rem;
    right: 1rem;
    top: 1rem;
    color: var(--primary);
    visibility: hidden;
    opacity: 0;
    transition: all 100ms 100ms 0 linear;
}
.module-tab.show .fa-check-circle {
    visibility: visible;
    opacity: 1;
    transition: all 250ms 300ms linear;
}

.repeat-items {
